interface TypingLineProps {
  line: number;
  content: string;
  delay: number;
  isVisible: boolean;
}

function TypingLine({ line, content, delay, isVisible }: TypingLineProps) {
  const getSyntaxHighlightedContent = (text: string) => {
    if (!text) return <span></span>;

    const parts = text.split(
      /(import|from|const|useState|useEffect|motion|React|return|className|div|span|button|h1|h2|h3|p|ul|li|a|img|src|alt|href|onClick|onChange|onSubmit|type|name|id|value|placeholder|disabled|required|optional|string|number|boolean|object|array|function|interface|type|enum|class|extends|implements|public|private|protected|static|readonly|abstract|async|await|Promise|then|catch|finally|try|catch|throw|new|this|super|extends|implements|interface|type|enum|class|public|private|protected|static|readonly|abstract|async|await|Promise|then|catch|finally|try|catch|throw|new|this|super|'|"|`|{|}|[|]|\(|\)|;|,|\.|:|<|>|=|\+|\-|\*|\/|%|!|&|\||\^|~|\?|:|\s+)/g
    );

    return parts.map((part, index) => {
      if (
        part.match(
          /^(import|from|const|useState|useEffect|motion|React|return)$/
        )
      ) {
        return (
          <span key={index} className="text-purple-300">
            {part}
          </span>
        );
      }
      if (part.match(/^(div|span|button|h1|h2|h3|p|ul|li|a|img)$/)) {
        return (
          <span key={index} className="text-blue-300">
            {part}
          </span>
        );
      }
      if (
        part.match(
          /^(className|src|alt|href|onClick|onChange|onSubmit|type|name|id|value|placeholder|disabled|required)$/
        )
      ) {
        return (
          <span key={index} className="text-cyan-400">
            {part}
          </span>
        );
      }
      if (
        part.match(
          /^(string|number|boolean|object|array|function|interface|type|enum|class)$/
        )
      ) {
        return (
          <span key={index} className="text-orange-300">
            {part}
          </span>
        );
      }
      if (part.match(/^['"`].*['"`]$/)) {
        return (
          <span key={index} className="text-green-400">
            {part}
          </span>
        );
      }
      if (part.match(/^[{}()[\].,;:<>=\+\-\*\/%!&|^~?]$/)) {
        return (
          <span key={index} className="text-white/60">
            {part}
          </span>
        );
      }
      return (
        <span key={index} className="text-white/70">
          {part}
        </span>
      );
    });
  };

  return (
    <motion.div
      initial={{ opacity: 0, x: -10 }}
      animate={{
        opacity: isVisible ? 1 : 0,
        x: isVisible ? 0 : -10,
      }}
      transition={{ delay, duration: 0.5 }}
      className="flex items-center group"
    >
      <span className="text-white/30 w-8 text-right mr-4">{line}</span>
      <span className="text-sm font-mono">
        {getSyntaxHighlightedContent(content)}
        {isVisible && content && (
          <motion.span
            className="text-cyan-400 font-bold ml-1"
            animate={{ opacity: [1, 0, 1] }}
            transition={{ duration: 1, repeat: Infinity }}
          >
            |
          </motion.span>
        )}
      </span>
    </motion.div>
  );
}
