"use client";

import { motion } from "framer-motion";
import {
  ArrowRight,
  Monitor,
  Star,
  GitBranch,
  ExternalLink,
  Play,
  Share,
} from "lucide-react";
import Header from "@/components/layout/Header";
import HeroSection from "@/components/sections/HeroSection";
import CodeEditorSection from "@/components/sections/CodeEditorSection";
import SuccessStoriesSection from "@/components/sections/SuccessStoriesSection";
import FeaturesSection from "@/components/sections/FeaturesSection";
import CTASection from "@/components/sections/CTASection";
import Footer from "@/components/layout/Footer";

export default function HomePage() {
  return (
    <main className="bg-black text-white min-h-screen">
      <Header />

      {/* Hero Section with Typing Animation */}
      <section className="pt-40 pb-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="max-w-4xl">
            {/* Trust Badge */}
            <div className="inline-flex items-center gap-3 px-4 py-2 bg-gradient-to-r from-blue-500/10 to-purple-600/10 border border-blue-500/20 rounded-full mb-8">
              <div className="w-2 h-2 rounded-full animate-pulse bg-cyan-400"></div>
              <span className="text-sm font-medium">
                Available for new opportunities
              </span>
            </div>

            {/* Main Heading */}
            <h1 className="text-5xl sm:text-6xl lg:text-7xl sf-pro-display tracking-tight mb-8 bg-gradient-to-b from-white to-white/70 bg-clip-text text-transparent font-light">
              Think different.
              <br />
              Build exceptional.
            </h1>

            {/* Description */}
            <p className="text-xl text-white/60 max-w-2xl leading-relaxed mb-12">
              Full-stack developer crafting digital experiences that matter.
              Specializing in modern web technologies and user-centered design.
            </p>

            {/* CTA Buttons */}
            <div className="flex flex-col sm:flex-row gap-4">
              <button className="group relative inline-flex items-center gap-2 px-8 py-4 bg-white text-black rounded-full font-semibold hover:bg-white/90 transition-all transform hover:scale-105">
                Get Started
                <svg
                  className="w-5 h-5 group-hover:translate-x-1 transition-transform"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M5 12h14m-7-7l7 7-7 7"
                  />
                </svg>
              </button>
              <button className="flex items-center gap-2 px-8 py-4 border border-white/20 rounded-full font-semibold hover:bg-white/10 transition-all">
                <svg
                  className="w-5 h-5"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <rect width="20" height="14" x="2" y="3" rx="2" />
                  <line x1="8" x2="16" y1="21" y2="21" />
                  <line x1="12" x2="12" y1="17" y2="21" />
                </svg>
                View My Work
              </button>
            </div>
          </div>
        </div>
      </section>

      {/* Interactive IDE Section */}
      <CodeEditorSection />

      {/* Success Stories Section */}
      <SuccessStoriesSection />

      {/* Features Grid */}
      <FeaturesSection />

      {/* CTA Section */}
      <CTASection />

      <Footer />
    </main>
  );
}
